import React, {useEffect, useState} from 'react';
import axios from 'axios';
import './App.css';
import Coin from './Coin';

function App() {

  const [coins, setCoins] =useState([]);
  const [search, setSearch] =useState('');

  useEffect(() => {
    axios
    .get(
      'https://api.coingecko.com/api/v3/coins/markets?vs_currency=cad&order=market_cap_desc&per_page=500&page=1&sparkline=false'
    )
    .then(res =>{
      setCoins(res.data);
      console.log(res.data);
    })
    .catch(error => console.log(error));
  }, []);

  const handleChange = e =>{
    setSearch(e.target.value)
  }

  const filteredCoins = coins.filter(coin =>
    coin.name.toLowerCase().includes(search.toLowerCase())
  )

  return (
    <div className="coin-app">
      <h1 className="coin-text">Over Hunderds of Crytpo-Currency at your fingertips</h1>
      <h1 className="coin-text">Crypto Maniacs</h1>

      <div className="coin-search">
        <form>
          <input type="text" placeholder="Search by Name" className="coin-input" onChange={handleChange}/>
        </form>
      </div>
      {filteredCoins.map(coin => {
        return (
          <Coin 
          key={coin.id}
          name={coin.name}
          price={coin.current_price}
          symbol={coin.symbol}
          marketcap={coin.market_cap}
          volume={coin.total_volume}
          image={coin.image}
          priceChange={coin.price_change_percentage_24h}
          />
        );
      })}
    </div>
  );
}

export default App;
